<template>
    <div id="aboutpage">
        <Introcard :inforSelf="inforSelf">

        </Introcard>
        <PlanList class="planlist" :hobbys="hobbys" @add="add" @flush="flush" @change="change" @alldone="alldone"
            @clearall="clearall"></PlanList>
        
    </div>
</template>
<script>


import Introcard from '@/components/content/introcard'

import { getinfor } from "@/network/aboutpage.js"

import PlanList from './planlist'


export default {
    name: 'aboutpage',
    data() {
        return {
            inforSelf: "",
            hobbys: [{ id: '01', content: '篮球', describe: '乘风破浪会有时', done: true },
            { id: '02', content: '羽毛球', describe: '梅花香自苦寒来', done: true }]
        }
    },
    components: {
        Introcard,
        PlanList
    },
    created() {
        getinfor().then(res => {
            this.inforSelf = res.config.baseURL
            console.log(res)
        })

    },
    methods: {
        add(value) {
            var hobby = {
                id: '0' + this.hobbys.length + 1,
                content: value,
                describe: '接天莲叶无穷碧',
                done: false
            }
            this.hobbys.push(hobby)
        },
        flush(value) {

            this.hobbys = this.hobbys.filter(item => item.id == value.id)

        },
        change(value) {
            this.hobbys = this.hobbys.filter((item) => {
                if (item.id == value.id) {
                    item.done = !item.done
                }
                return true
            })
        },
        alldone(value) {

            this.hobbys.forEach((item) => {
                item.done = value
            })
        },
        clearall() {
            this.hobbys = []
            
        }
    },

}
</script>
<style scoped lang="less">
#aboutpage {
    padding: 10px;

    .planlist {
        margin-top: 10px;
    }
}
</style>


