<template>
    <div id="introcard">
        <div class="wrapper">
            <div class="header">
                <img src='~assets/img/imagesshow/wallpaper01.jpg' alt="">
                
            </div>
            <div class="info">
                <h2> tony</h2>
                <p>简介：{{inforSelf}}</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'introcard',
    props:{
        inforSelf:{
            type:String,
            default(){
                return "友好"
            }
        }
    }
}
</script>
<style scoped lang="less">
#introcard{

    
    .wrapper{
        width:300px;
        height:300px;
        margin:auto;
        display: flex;
        flex-direction: column;
        border:2px solid rgba(100, 100, 100, .4);
        padding:5px;
        .header{
            height:100px;
            text-align: center;
            
            img{
                    width:100px;
                    height:100px;
                    border-radius: 50%;
            }
    
           
        }
        .info{
            flex:1;
            text-align: center;
            p{
                
            }

        }
    }
}

</style>


